import Vue from 'vue'
import App from './App.vue'
import router from './router'
import VueRouter from 'vue-router'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

//:mint-ui
//1.mint-ui
import MintUI from "mint-ui"
//2.mint-ui
import "mint-ui/lib/style.css"

//3.
Vue.use(MintUI)
Vue.use(ElementUI);
Vue.use(VueRouter);

import axios from "axios"
//7.
// axios.defaults.baseURL="http://1.kitnode.applinzi.com/"
axios.defaults.baseURL="http://www.xp521.xyz/"
//8.session
axios.defaults.withCredentials=true;
//9.axiosvue
Vue.prototype.axios = axios;
//10.vuex
//11.vuex
import Vuex from "vuex"
//12.vuex
Vue.use(Vuex);
//13.
var store = new Vuex.Store({
  //(1).
  state:{
    ddage:21,  //
    cartCount:0, //
  },
  //(2).
  mutations:{
    //
    addCartCount(state,num){
      state.cartCount+=num;
    },
    //
    subCartCount(state,num){
      state.cartCount-=num;
    },
    //
    clearCartCount(state,num){
      state.cartCount=0;
    },
    addAge(state){state.ddage++}, //
    subAge(state){state.ddage--},
    modifyAge(state,num){ //
      state.ddage-=num;
    },
  },
  //(3).
  getters:{
    getAge(state){return state.ddage}, //
    getCartCount(state){return state.cartCount} //
  },
  //(4).
})

new Vue({
  router,
  render: h => h(App),  //App App.vue    
  //14.Vue    
  store     
}).$mount('#app')
