<template>
    <section>
    <div class="login">
        <div class="login-box">
            <div class="bgimg" @click="toIndex()">
                <a href=""></a>
            </div>
            <div class="login-log">
                <table>
                    <tr>
                        <td>
                            <h1></h1>
                        </td>
                        <td align="right">
                            <a href="javascript:;" @click="Login()"></a>
                        </td>
                    </tr>
                    <tr>
                        <td colspan="2">
                            <input autocomplete="off" :data-uname="uname" v-model="uname" type="text" id="uname" placeholder="//" style="margin-top:25px;">
                            <span style="font-size:12px;color:#888888;margin-left:15px;">3~12</span>
                        </td>
                    </tr>
                    <tr>
                        <td colspan="2">
                            <input autocomplete="off" :data-upwd="upwd" v-model="upwd" type="password" id="upwd" placeholder="">
                            <span style="font-size:12px;color:#888888;margin-left:15px;">6~18</span>
                        </td>
                    </tr>
                    <tr>
                        <td colspan="2">
                            <input autocomplete="off" :data-upwd="upwd" v-model="cpwd" type="password" id="cpwd" placeholder="">
                        </td>
                    </tr>
                    <tr>
                        <td colspan="2">
                            <input type="text" id="pass" placeholder="">
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <input style="width:18px;background-color:#000;cursor:pointer;vertical-align:middle" class="autologin" type="checkbox" id="auto">
                            <label for="auto"></label>
                            <a @click="agreement()" href="javascript:;" style="color:#000;font-weight:bold;"></a>
                        </td>
                    </tr>
                    <tr>
                        <td colspan="2">
                            <button @click="reg()"></button>
                        </td>
                    </tr>
                </table>
            </div>
        </div>
    </div>
    <myfooter></myfooter>
    </section>
</template>
<script>
import MyFooter from './MyFooter';
import { Popup } from 'mint-ui';

export default {
    components:{
        "myfooter":MyFooter,
    },
    data(){
        return {
            uname:"",
            upwd:"",
            cpwd:"",
        }
    },
    methods:{
        agreement() {
            this.$alert(`anitoys:anitoys""www.anitoys.com"anitoys"anitoys""1 1.1anitoys1.21.3181.4anitoys22.1anitoys2.2122.3 anitoys2.4anitoysanitoysanitoys""`, '');
        },
        toIndex(){
            this.$router.push("/Index");
        },
        Login(){
            this.$router.push("/Login");
        },
        reg(){
            var reg = /^[a-zA-Z0-9]{3,12}$/;
            var regUpwd = /^(?=.*[a-zA-Z])(?=.*[0-9])[A-Za-z0-9]{6,18}$/;
            var uname = this.uname;
            var upwd = this.upwd;
            var cpwd = this.cpwd;
            if(!reg.test(uname)){
                alert("");
                return;
            }
            if(!regUpwd.test(upwd)){
                alert("");
                return;
            }
            if(upwd!=cpwd){
                alert("");
                return;
            }
            //7.ajax
            var url = "reg";
            var obj = {uname,upwd};
            this.axios.get(url,{params:obj})
            .then(res=>{
                if(res.data.code==-2){
                    alert("");
                }else{
                    alert("!");
                    return;
                    this.$router.push("/Index");
                }
            })
        }
    }
}
</script>
<style scoped>
    .login{
        width:100%;
        background-color:#fff;
    }
    .login-box{
        width:1180px;height:500px;
        margin:0 auto;
        padding:200px 0 200px 0px;
    }
    .login .login-box .bgimg{
        width:700px;height:500px;
        background-image: url("../img/login/login-img.jpg");
        background-repeat: no-repeat;
        background-size: 72%;
        background-position: center;
        float: left;
        cursor: pointer;
    }
    .login-log{
        width:370px;height:100px;
        float: right;
    }
    .login-log table{
        width:100%;
        margin-left:-85px;
        margin-top: 30px;
    }
    a{
        text-decoration: none;
        font-size: 14px;
        color:#888888;
    }
    a:hover{
        color:#F38317;
    }
    h1{
        font-size:28px;
    }
    #uname,#upwd,#cpwd,#pass{
        width:370px;height:40px;
        outline:0;
        padding-left:15px;
        border-radius:3px;
        box-sizing: border-box;
    }
    #upwd{
        margin-top:10px;
    }
    #cpwd{
        margin-top:10px;
    }
    #pass{
        margin-top:10px;
        background-image: url("/public/img/login/rand.jpg");
        background-repeat: no-repeat;
        background-position: 97%;
        background-size:25%;
    }
    .login-log table input{
        width:21px;height:21px;
        border: 1px solid #D9DDE0;
        margin-bottom:1px;
        font-size:13px;
    }
    .login-log table label{
        font-size:14px;
    }
    .login-log table button{
        width:370px;height:40px;
        border-radius:4px;
        background-color:#FF6600;
        outline:0;
        border:0;
        font-size:16px;
        color:#fff;
        margin-top:15px;
        cursor: pointer;
    }
</style>