import Vue from 'vue'
import Vuex from 'vuex'

// vuex 
Vue.use(Vuex)
const key = 'user'
const store =  new Vuex.Store({
  // 
  state: {
    user:null,
    cartProducts: [
      
    ]
  },
  //  mutations
  mutations: {
    //   localStorage
    _setStorage(state,value){ 
      state.user = value
      localStorage.setItem(key,JSON.stringify(value))  // localStroage.setItem(key,) 
    },
    _removeStorage(state){
      state.user = null
      localStorage.removeItem(key)  // localStorage.removeItem(key) 
    }
  },
  actions: {

  },
  // getters 
  getters:{ 
    getStorage(state){
      if(!state.user){
        state.user = JSON.parse(localStorage.getItem(key)) // localStorage.getItem(key) // 
      }
      return state.user
    }
  }
})
export default store